import Axios from "axios";
import Vue from "vue";

const env = process.env.NODE_ENV;

const instance = Axios.create({
  withCredentials: true,
  timeout: 20000
});

instance.interceptors.request.use(function(config) {
  console.log("node env", env);
  console.log(process.env.VUE_APP_MY_DOMAIN, "my domain");
  if (env === "production") {
    let baseUrl = process.env.VUE_APP_MY_DOMAIN;
    const paths = config.url.split("/");
    console.log("paths", paths);
    if (paths[1] === "proposal") {
      config.url = `${baseUrl}/` + paths.slice(2).join("/");
    }
  }
  return config;
});

export const $http = instance;

function axios(options) {
  let cookie = null;
  if (options.cookie) {
    cookie = `${options.cookie.key}=${options.cookie.value}`;
    delete options.cookie;
  }
  const instance = Axios.create(
    Object.assign(
      {
        timeout: 4000
      },
      options,
      cookie
        ? {
            headers: {
              Cookie: cookie
            }
          }
        : {}
    )
  );
  // instance.interceptors.request.use(function (config) {
  //   const paths = config.url.split('/');
  //   if (paths[0] === 'proposal') {
  //     config.url = 'http://test-mnta.imweiyuan.com/' + paths.slice(1).join('/');
  //   }
  //   return config;
  // });
  return instance;
}

function plugin(Vue) {
  if (plugin.installed) {
    return;
  }
  Vue.http = instance;
  Vue.axios = axios;
  Object.defineProperty(Vue.prototype, "$http", {
    value: instance
  });
  Object.defineProperty(Vue.prototype, "$axios", {
    value: axios
  });
}

if (typeof window !== "undefined" && window.Vue) {
  window.Vue.use(plugin);
}

export default plugin;
