import {
  $http
} from '@/assets/plugin/axios.js';

export const userLogin = ({
  code,
  phone
}) => {
  return $http.post('/proposal/api/v1/phoneAuth/', {
    code,
    phone
  });
};
export const code = ({
  phone
}) => {
  return $http.get(`/proposal/api/v1/phoneAuth/code/${phone}`, {
    phone
  });
};
export const getCodeImg = ({
  v
}) => {
  return $http.get('/proposal/api/v1/wechatAuth/qrcode', {
    v
  });
};
export const wxAuth = ({
  code
}) => {
  return $http.get(`/proposal/api/v1/wechatAuth/authToken/${code}`, {
    code
  });
};

export const getUserInfo = () => $http.get(`/proposal/api/v1/user/profile`);
export const editUserInfo = (form) => $http.patch(`/proposal/api/v1/user/profile`, form);
export const getNations = () => $http.get(`/proposal/api/v1/user/nations`);
export const getPolitics = () => $http.get(`/proposal/api/v1/user/politics`);
export const getRegion = ({
  province,
  city
}) => {
  let url = `/proposal/api/v1/region`;
  if (province) {
    url += `/${province}`;
    if (city) {
      url += `/${city}`
    }
  }
  return $http.get(url);
}
export const userLogout = () => {
  return $http.delete('/proposal/api/v1/auth');
};

export const userGetBindPhoneCode = ({
  phone
}) => {
  return $http.get(`/proposal/api/v1/user/phone/code/${phone}`);
};


export const userBindPhone = ({
  phone,
  code
}) => {
  return $http.patch(`/proposal/api/v1/user/phone`, {
    phone,
    code
  });
};

