<template>
  <div class="login_switch">
    <template v-if="userLogin">
      <ul class="login-switch">
        <li @click="goUserLogin">用户登录</li>
        <li @click="goTWLogin">团委登录</li>
      </ul>
      <form @submit.prevent="">
        <div @click="goWxLogin" class="wx_login">
          <svg t="1598229835178" class="wx-login__icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1733" width="200" height="200"><path d="M1024 619.52c0-143.36-138.24-256-307.2-256s-307.2 112.64-307.2 256 138.24 256 307.2 256c30.72 0 61.44-5.12 92.16-10.24l97.28 51.2-25.6-76.8c87.04-51.2 143.36-128 143.36-220.16z m-414.72-40.96c-30.72 0-51.2-20.48-51.2-51.2s20.48-51.2 51.2-51.2 51.2 20.48 51.2 51.2c0 25.6-25.6 51.2-51.2 51.2z m209.92 0c-30.72 0-51.2-20.48-51.2-51.2s20.48-51.2 51.2-51.2 51.2 20.48 51.2 51.2c0 25.6-25.6 51.2-51.2 51.2z" fill="#4CBF00" p-id="1734"></path><path d="M358.4 609.28c0-158.72 153.6-286.72 348.16-286.72h15.36c-40.96-133.12-179.2-235.52-353.28-235.52-204.8 0-368.64 138.24-368.64 307.2 0 107.52 66.56 204.8 168.96 256l-30.72 92.16L256 686.08c35.84 10.24 71.68 15.36 112.64 15.36h10.24c-15.36-30.72-20.48-61.44-20.48-92.16z m138.24-414.72c35.84 0 66.56 30.72 66.56 66.56s-30.72 66.56-66.56 66.56C460.8 322.56 430.08 291.84 430.08 256S460.8 194.56 496.64 194.56zM245.76 322.56c-35.84 0-61.44-30.72-61.44-66.56s30.72-66.56 66.56-66.56 61.44 30.72 61.44 66.56-30.72 66.56-66.56 66.56z" fill="#4CBF00" p-id="1735"></path></svg>
          <span class="wx-login__text">微信一键登录</span>
        </div>
        <div class="login_hr">
          <div class="login_hr__left"></div>
          <div class="login_hr__text">或</div>
          <div class="login_hr__right"></div>
        </div>
        <div class="row form-item">
          <div class="box">
            <input class="user-input" @blur="validatePhone" name="phone" required minlength="11" maxlength="11" placeholder="请输入手机号" type="text" v-model="phone" />
          </div>
          <div v-if="!isPhoneValid" class="tips">
            <svg viewBox="0 0 20 20" fill="currentColor" class="x-circle w-6 h-6"><path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clip-rule="evenodd"></path></svg>
            <span>请输入正确的手机号</span>
          </div>
        </div>
        <div class="row form-item">
          <input class="user-input get-code-input" name="code" placeholder="请输入验证码" type="text" v-model="code" />
          <button @click="getCode" class="btn2 get-code-btn" :disabled="block">{{block ? countdown : '获取验证码'}}</button>
        </div>
        <div @click="login()" class="click_login btn2">登录</div>
        <div class="login_tips">
          登录即表示统一
          <router-link to="/tos">《使用条款》</router-link>
        </div>
      </form>
    </template>
    <template v-if="wxLogin">
      <div v-if="!hasScan" class="wxCode wx-login">
        <div class="wx-code__back" @click="toUserLogin">
          <svg viewBox="0 0 20 20" fill="currentColor" class="arrow-left w-6 h-6"><path fill-rule="evenodd" d="M9.707 16.707a1 1 0 01-1.414 0l-6-6a1 1 0 010-1.414l6-6a1 1 0 011.414 1.414L5.414 9H17a1 1 0 110 2H5.414l4.293 4.293a1 1 0 010 1.414z" clip-rule="evenodd"></path></svg>
          <span>返回</span>
        </div>
        <div class="wx-login__title">微信扫一扫登录</div>
        <img width="220" height="220" :src="imgUrl" />
        <div class="wx-login__info">扫码关注公众号完成登录</div>
      </div>
      <div v-if="(hasScan && !hasPhone)" class="bind-phone">
        <div class="wx-code__back" @click="toWechatLogin">
          <svg viewBox="0 0 20 20" fill="currentColor" class="arrow-left w-6 h-6"><path fill-rule="evenodd" d="M9.707 16.707a1 1 0 01-1.414 0l-6-6a1 1 0 010-1.414l6-6a1 1 0 011.414 1.414L5.414 9H17a1 1 0 110 2H5.414l4.293 4.293a1 1 0 010 1.414z" clip-rule="evenodd"></path></svg>
          <span>返回</span>
        </div>
        <div class="row form-item bind-phone__body">
          <div class="box">
            <input class="user-input" @blur="validatePhone" name="phone" required minlength="11" maxlength="11" placeholder="请输入手机号" type="text" v-model="phone" />
          </div>
          <div v-if="!isPhoneValid" class="tips">
            <svg viewBox="0 0 20 20" fill="currentColor" class="x-circle w-6 h-6"><path fill-rule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clip-rule="evenodd"></path></svg>
            <span>请输入正确的手机号</span>
          </div>
        </div>
        <div class="row form-item">
          <input class="user-input get-code-input" name="code" placeholder="请输入验证码" type="text" v-model="code" />
          <button @click="wechatUserGetPhoneCode" class="btn2 get-code-btn" :disabled="block">{{block ? countdown : '获取验证码'}}</button>
        </div>
        <div @click="doBindPhone" class="click_login btn2">绑定手机</div>
      </div>
    </template>
  </div>
</template>
<script>
import { userLogin, getCodeImg, code, wxAuth, userGetBindPhoneCode, userBindPhone } from "@/api/user";
export default {
  data() {
    return {
      hasScan: false,
      hasPhone: true,
      isPhoneValid: true,
      twLogin: false,
      userLogin: false,
      wxLogin: false,
      errPhone: true,
      success: true,
      phone: "",
      code: "",
      imgUrl: "",
      authAskCode: "",
      block: false,
      countdown: 0
    };
  },
  created() {
    this.phone = "";
    this.code = "";
  },
  methods: {
    toWechatLogin() {
      this.hasScan = false;
      this.hasPhone = true;
    },
    doBindPhone() {
      let phone = this.phone;
      let code = this.code;
      userBindPhone({
        phone,
        code
      })
      .then(res => {
        console.log('user bind phoen result', res);
      })
    },  
    validatePhone(e) {
      let value = e.target.value;
      if (!value || value.length !== 11) {
        this.isPhoneValid = false;
      } else {
        this.isPhoneValid = true;
      }
    },
    toUserLogin() {
      this.userLogin = true;
      this.wxLogin = false;
    },
    goUserLogin() {
      this.userLogin = true;
      this.twLogin = false;
    },
    goWxLogin() {
      this.userLogin = false;
      this.wxLogin = true;
      getCodeImg({
        v: 111
      }).then(res => {
        this.imgUrl = res.data.data.qrcodeImage;
        this.authAskCode = res.data.data.authAskCode;
        this.wechatAuth();
      });
    },
    wechatAuth() {
      const ask = () => {
        if (!this.wxLogin) return;
        if (!this.authAskCode) return;
        wxAuth({
          code: this.authAskCode
        })
        .then(res => {
          if (+res.data.code === 200) {
            this.hasScan = true;
            this.hasPhone = false;
            // TODO: redirect to user home
            // let data = res.data;
            // if (!data || !data.phone) {
            //   this.hasPhone = false;
            // }
          } else {
            setTimeout(() => {
              ask()
            }, 5000);
          }
        })
        .catch(err => {
          console.error(err);
        })
      }
      ask();
    },
    goTWLogin() {
      this.$router.push("/TwLogin");
    },
    login() {
      if (this.phone && this.code) {
        userLogin({
          phone: this.phone,
          code: this.code
        }).then(res => {
          if(res.code = 200){
              alert("登录成功");
          }
        });
      }
    },
    wechatUserGetPhoneCode() {
      if (this.block) return;
      if (this.phone) {
        userGetBindPhoneCode({
          phone: this.phone
        }).then(res => {
          if (+res.data.code === 200) {
            this.block = true;
            this.countdown = 5;
            let tick = () => {
              this.countdown -= 1;
              console.log(this.countdown);
              if (this.countdown <= 0) {
                this.block = false;
              } else {
                setTimeout(() => {
                  tick();
                }, 1000);
              }
            }
            setTimeout(() => {
              tick();
            }, 1000);
          }
        });
      }
    },
    getCode() {
      if (this.block) return;
      if (this.phone) {
        code({
          phone: this.phone
        }).then(res => {
          if (+res.data.code === 200) {
            this.block = true;
            this.countdown = 5;
            let tick = () => {
              this.countdown -= 1;
              console.log(this.countdown);
              if (this.countdown <= 0) {
                this.block = false;
              } else {
                setTimeout(() => {
                  tick();
                }, 1000);
              }
            }
            setTimeout(() => {
              tick();
            }, 1000);
          }
        });
      }
    }
  },
  created() {
    this.userLogin = true;
  }
};
</script>
<style lang="less" scoped>
.wx-login {
  &__title {
    font-size: 32px;
    color: #2D2D2D;
    line-height: 45px;
    font-weight: bold;
    text-align: center;
  }
  img {
    width: 220px;
    height: auto;
    display: block;
    margin: 0 auto;
    margin-top: 26px;
  }
  &__info {
    font-size: 14px;
    color: #7C7C7C;
    line-height: 20px;
    text-align: center;
    margin-top: 10px;
  }
}
.get-code-btn {
      display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    width: 110px;
    flex-shrink: 0;
    flex-grow: 0;
}

  .row {
    display: flex;
  }
  .box {
    position: relative;
    flex: 1;
  }
  .item {
    position: absolute;
    margin-left: 10px;
  }
  .item img {
  }
  button {
    border-radius: 6px;
    border: none;
    outline: none;
    color: #fff;
  }
  .successImg {
    width: 10px;
    height: 10px;
    position: absolute;
    top: 18px;
    right: 0;
  }
  .form-item {
    position: relative;
  }
  .form-item + .form-item {
    margin-top: 20px;
  }
  .user-input {
    width: 100%;
    display: flex;
    border-radius: 6px;
