    justify-content: center;
  }
  .tips {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: absolute;
    left: calc(100% + 6px);
    line-height: 56px;
    width: max-content;
    .x-circle {
      width: 14px;
      height: 14px;
      margin-right: 6px;
      fill:  #FF543C;
    }
    color: #FF543C;
    font-size: 12px;
  }
  .login_tips {
    text-align: left;
    margin-top: 20px;
    font-size: 12px;
    
    a {
